<template>
  <div class="page">
    <div class="answer-tab">
      <span :class="{'active':curTab == 1}" @click="changeTab(1)">我的提问 {{questionCount}}</span>
      <span :class="{'active':curTab == 2}" @click="changeTab(2)">我的回答 {{answerCount}}</span>
    </div>
    <div class="answer-list" v-if="list.length">
      <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <div class="answer-item" v-for="item in list" :key="item.questionId">
            <div class="item-shop" @click="goDetail(item.productId)">
              <img :src="item.defaultImageUrl" alt="">
              <div class="shop-info">
                <h4>{{item.productFullName}}</h4>
                <!-- <p>共{{item.answerCount}}个回答</p> -->
              </div>
            </div>
            <div class="item-question" @click="goAnswerDetail(item)">
              <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/answer/question.png" alt="">
              <div>
                <h5>{{item.question}}</h5>
                <p>
                  <span v-if="curTab == 1">{{item.createTime || item.answerTime}} {{item.answerCount}}个回答</span>
                  <span v-else-if="curTab == 2">{{item.nickName}}: {{item.answer}}</span>
                  <a href="javascript:;" @click.stop="doDelete(item)">删除<i></i></a>
                </p>
              </div>
            </div>
          </div>
      </van-list>
    </div>
    <AnswerEmpty v-if="!list.length && finished"/>
  </div>
</template>

<script>
import AnswerEmpty from './component/answer-empty'
import { getPageMemberAnswer, getPageMemberQuestion, deleteProductQuestion, deleteProductAnswer, getMyQuestionAnswer } from '@/http/service/goods'
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: 'goodsanswermine',
  components: {
    AnswerEmpty
  },
  data() {
    return {
      curTab: 1,
      curPage: 1,
      pageSize: 20,
      list: [],
      loading: false,
      finished: false,
      questionCount: '',
      answerCount: ''
    }
  },
  computed: {
    ...mapGetters(['mId']),
  },
  activated () {
    this.resetInit()
  },
  methods: {
    ...mapMutations('answerArea', ['setQuestionInfo']),
    getMyQuestionAnswer() {
      getMyQuestionAnswer().then(res => {
        let { result } = res
        this.questionCount = result.questionCount
        this.answerCount = result.answerCount
      })
    },
    resetInit() {
      this.curPage = 1;
      this.list = [];
      this.loading = false
      this.finished = false
      if (this.curTab === 1) {
        this.getPageMemberQuestion()
      } else if (this.curTab === 2) {
        this.getPageMemberAnswer()
      }
      this.getMyQuestionAnswer()
    },
    changeTab(tab) {
      this.curTab = tab;
      this.resetInit();
    },
    onLoad() {
      this.getPageMemberAnswer()
    },
    // 我的提问
    getPageMemberQuestion() {
      this.loading = true
      let params = {
        start: this.curPage,
        offset: this.pageSize,
        answerUserId: this.mId,
      }
      getPageMemberQuestion(params).then(res => {
        let { result, success } = res;
        if (success) {
          // this.questionInfo =
          this.curPage += 1;
          this.loading = false
          this.list = [...this.list, ...result]
          this.finished = result.length < this.pageSize
          // this.questionCount = pager.rowsCount
        } else {
          this.loading = false
          this.finished = true
        }
      })
    },
    // 我的回答
    getPageMemberAnswer() {
      this.loading = true
      let params = {
        start: this.curPage,
        offset: this.pageSize,
        answerUserId: this.mId,
      }
      getPageMemberAnswer(params).then(res => {
        let { result, success } = res;
        if (success) {
          // this.questionInfo =
          this.curPage += 1;
          this.loading = false
          this.list = [...this.list, ...result]
          this.finished = result.length < this.pageSize
          // this.answerCount = pager.rowsCount
        } else {
          this.loading = false
          this.finished = true
        }
      })
    },

    // 删除提问或回答
    doDelete(item) {
      this.$dialog.confirm({
        message: '确定要删除吗?',
        confirmButtonText: '确定'
      }).then(() => {
        if (this.curTab === 1) {
          deleteProductQuestion({
            questionId: item.questionId
          }).then(res => {
            let { success, message } = res
            if (success) {
              this.$toast('删除成功')
              this.resetInit()
            } else {
              this.$toast(message)
            }
          })
        } else if (this.curTab === 2) {
          deleteProductAnswer({
            answerId: item.answerId,
            questionId: item.questionId
          }).then(res => {
            let { success, message } = res
            if (success) {
              this.$toast('删除成功')
              this.resetInit()
            } else {
              this.$toast(message)
            }
          })
        }
      })
    },

    goDetail(productId) {
      this.$router.push('/goodsDetail?productId=' + productId)
    },
    goAnswerDetail(item) {
      console.log(item, 'goAnswerDetail')
      this.setQuestionInfo(item)
      this.$router.push('/goodsAnswerDetail/' + item.questionId)
    }
  }
}
</script>

